	}

	.subject {
		margin-top: 50px;
	}

	.bottom {
		text-align: center;
		background: #fff;
		border: #e2e1e1 1px solid;
		width: 100%;
		position: fixed;
		right: 0;
		z-index: 500;
		bottom: 0;
	}

	.bottom-item {
		color: #808080;
		width: 49%;
		display: block;
